.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* Variables */

:root {
  /* Colores */
  --turquoise: #6DD3CE;
  --yellow: #C8E9A0;
  --lightSalmon: #F7A278;
  --marron: #A13D63;
  --darkPurple: #560bad;
  --white: #FFFFFE;
  --black:#000;
}

html {
  box-sizing: border-box;
}

/* Fonts */

h2 { 
  font-size: 18px;
  font-weight: 700; 
  text-align: center;
}
h3 { 
  font-size: 18px;
  font-weight: 600; 
}
h4 { 
  font-size: 12px;
  font-weight: 600; 
}
p{
  font-size: 16px;
  font-weight: 400; 
} 